.bestchange {
    background: #0d151e;
    border-radius: 10px;
    margin: 20px 0 0 0;
    padding: 15px 20px 15px 170px;
    color: #fff;
    position: relative;
    font-size: 15px;
    background: -webkit-linear-gradient(90deg,rgba(15,21,29,.8) 0,rgba(15,21,29,.5) 100%);
    color: #848f9e;
    color: #9fa9b5;
    position: relative;
    z-index: 9;
}
.bestchange h5 {
    color: #8bbd06;
    color: #3dd94b;
    color: #fff;
    font-size: 19px;
    margin-bottom: 6px;
}
.desc a {
    color: #0facf3;
}
.desc b {
    font-style: normal;
    color: #3dd94b;
}
.logo {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 150px;
    background: #8bbd06;
    background: #8bbd06;
    background: linear-gradient(180deg,#8bbd06 0,#70960c 100%);
    border-radius: 10px 0 0 10px;
    padding: 15px 15px 0 20px;
}
.button {
    position: absolute;
    top: 20px;
    right: 20px;
}
