.button .btn {
    color: #304550;
    position: relative;
    text-align: center;
    font-size: 14px;
    display: inline-block;
    text-decoration: none;
    height: 36px;
    line-height: 34px;
    padding: 0 20px;
    font-weight: 700;
    min-width: 80px;
    outline: 0;
    resize: none;
    border: none;
    overflow: hidden;
    -webkit-appearance: none;
    -webkit-transition: all ease-out .1s;
    -moz-transition: all ease-out .1s;
    -ms-transition: all ease-out .1s;
    -o-transition: all ease-out .1s;
    transition: all ease-out .1s;
    background: #fff;
    box-shadow: 0 3px 8px 0 rgb(103 116 116 / 20%);
    border-radius: 100px;
    -moz-border-radius: 100px;
    -webkit-border-radius: 100px;
    margin-top: -1px;
    margin-bottom: 1px;
}

@media (max-width: 860px) {
    .bestchange {
        display: none;
    }
}